import Endpoint from '@/services/Endpoint'

export default {
  lgTypes (payload) {
    return Endpoint().get('/s1/tradeFinance/lgType', {
      params: payload
    })
  },
  createLgType (payload) {
    return Endpoint().post('/s1/tradeFinance/lgType', payload)
  },
  modifyLgType (payload) {
    return Endpoint().patch('/s1/tradeFinance/lgType', payload)
  }
}
